.map__wrapper {
  @include reset-indents();
  position: relative;
  height: 807px;
  border-bottom: 2px solid $main-color;
  overflow: hidden;
}

.map__title {
  display: none;
}

.map__iframe,
.map__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.map__image {
  @include reset-indents();
  z-index: 5;
}

.map__iframe {
  z-index: 10;
  height: 805px;
  border: none;
}

@media (max-width: $desktop-width) {
  .map__wrapper {
    height: 350px;
  }

  .map__iframe {
    height: 348px;
  }
}

@media (max-width: $tablet-width) {
  .map__wrapper {
    height: 200px;
  }

  .map__title {
    display: block;
    padding: 0 6%;
    margin-bottom: 20px;
  }

  .map__iframe {
    z-index: 10;
    height: 198px;
  }
}
